import React from 'react';
import Section from './Section'
import InputField from './InputField'

class General extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          edit: true,
          email: '',
          gender: '',
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
      handleChange(e) {
        this.setState({
          [e.target.name]: e.target.value,
        });
      }
      handleSubmit(e) {
        e.preventDefault();
        let edit = this.state.edit === true ? false : true;
        this.setState({
          edit,
        });
      }
      render() {
        const { edit, email, gender } = this.state;
        return (
            <Section headerTitle="General Information">
              {edit ? (
                <form onSubmit={this.handleSubmit}>
                  <InputField
                    fieldName="email"
                    label="Email"
                    type="email"
                    placeholder="example@gmail.com"
                    onChange={this.onChange}
                    value={email}
                    onChange={this.handleChange}
                  />
                  <InputField
                    fieldName="gender"
                    type="text"
                    label="gender"
                    placeholder="Male/Female/Binary"
                    value={gender}
                    onChange={this.handleChange}
                  />
                  <button type="submit" className="save">
                    Save
                  </button>
                </form>
              ) : (
                <form onSubmit={this.handleSubmit}>
                    <span className="label">Email: <span className="value">{this.state.email}</span></span>
                    <span className="label">Gender: <span className="value">{this.state.gender}</span></span>
                  <button type="submit" className="save">
                    Edit
                  </button>
                </form>
              )}
            </Section>
        );
      }
}
export default General