import React from 'react';
import Section from './Section';
import InputField from './InputField';

class Work extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: true,
      companyName: '',
      positionTitle: '',
      tasks: '',
      dateFrom: '',
      dateTo: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  handleSubmit(e) {
    e.preventDefault();
    let edit = this.state.edit === true ? false : true;
    this.setState({
      edit,
    });
  }
  render() {
    const {
      edit,
      companyName,
      positionTitle,
      tasks,
      dateFrom,
      dateTo,
    } = this.state;
    return (
      <Section headerTitle="Work Experience">
        {edit ? (
          <form onSubmit={this.handleSubmit}>
            <InputField
              onChange={this.handleChange}
              fieldName="companyName"
              label="Company Name"
              placeholder="Name of the company"
              value={companyName}
            />
            <InputField
              onChange={this.handleChange}
              fieldName="positionTitle"
              label="Position Title"
              placeholder="Your position at the company"
              value={positionTitle}
            />
            <InputField
              onChange={this.handleChange}
              fieldName="tasks"
              label="Tasks"
              placeholder="Main tasks of your job"
              value={tasks}
            />
            <InputField
              onChange={this.handleChange}
              fieldName="dateFrom"
              label="From"
              type="date"
              value={dateFrom}
            />
            <InputField
              onChange={this.handleChange}
              fieldName="dateTo"
              label="To"
              type="date"
              value={dateTo}
            />
            <button type="submit" className="save">
              Save
            </button>
          </form>
        ) : (
          <form onSubmit={this.handleSubmit}>
            <span className="label">
              Company Name: <span className="value">{companyName}</span>
            </span>
            <span className="label">
              Position: <span className="value">{positionTitle}</span>
            </span>
            <span className="label">
              Tasks: <span className="value">{tasks}</span>
            </span>
            <span className="label">
              From: <span className="value">{dateFrom}</span>
            </span>
            <span className="label">
              To: <span className="value">{dateTo}</span>
            </span>
            <button type="submit" className="save">
              Edit
            </button>
          </form>
        )}
      </Section>
    );
  }
}

export default Work;
