import React from 'react';
import './ProfileCard.css';
import InputField from './InputField';

function Edit(props) {
  return (
    <div className="card">
      <form onSubmit={props.onSubmit}>
        <h1>Profile</h1>
        {props.children}
        <button type="submit" className="save">
          Save
        </button>
      </form>
    </div>
  );
}
function Avatar(props) {
  return (
    <label htmlFor="photo-upload" className="custom-file-upload fas">
      <div className="img-wrap img-upload">
        <img htmlFor="photo-upload" src={props.src} alt="uploader" />
      </div>
      <input id="photo-upload" type="file" onChange={props.onChange} />
    </label>
  );
}

function Profile(props) {
  return (
    <div className="card">
      <form onSubmit={props.onSubmit}>
        <label className="costum-file-upload fas">
          <div className="img-wrap">
            <img htmlFor="photo-upload" src={props.src} alt="avatar-holder" />
          </div>
        </label>
        <div className="name">{props.name}</div>
        <div className="profession">{props.profession}</div>
        <button type="submit" className="edit">
          Edit Profile
        </button>
      </form>
    </div>
  );
}
class ProfileCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      file: '',
      imagePreviewUrl:
        'https://d11a6trkgmumsb.cloudfront.net/original/3X/d/8/d8b5d0a738295345ebd8934b859fa1fca1c8c6ad.jpeg',
      name: '',
      profession: '',
      active: 'edit',
    };
    this.photoUpload = this.photoUpload.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  photoUpload(e) {
    e.preventDefault();
    const reader = new FileReader();
    const file = e.target.files[0];
    reader.onloadend = () => {
      this.setState({
        file: file,
        imagePreviewUrl: reader.result,
      });
    };
    reader.readAsDataURL(file);
  }
  handleSubmit(e) {
    e.preventDefault();
    let activeCard = this.state.active === 'edit' ? 'profile' : 'edit';
    this.setState({
      active: activeCard,
    });
  }
  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  render() {
    const { imagePreviewUrl, name, profession, active } = this.state;
    return (
      <div>
        {active === 'edit' ? (
          <Edit onSubmit={this.handleSubmit}>
            <Avatar onChange={this.photoUpload} src={imagePreviewUrl} />
            <InputField
              onChange={this.handleChange}
              value={name}
              label="name"
              fieldName="name"
              placeholder="John Doe"
            />
            <InputField
              onChange={this.handleChange}
              value={profession}
              fieldName="profession"
              label="profession"
              placeholder="Front End, Data scientist..."
            />
          </Edit>
        ) : (
          <Profile
            onSubmit={this.handleSubmit}
            src={imagePreviewUrl}
            name={name}
            profession={profession}
          />
        )}
      </div>
    );
  }
}

export default ProfileCard;
