import React from 'react';
import Section from './Section';
import InputField from './InputField';

class Education extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: true,
      schoolName: '',
      titleOfStudy: '',
      dateFrom: '',
      dateTo: '',
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  handleSubmit(e) {
    e.preventDefault();
    let edit = this.state.edit === true ? false : true;
    this.setState({
      edit,
    });
  }
  render() {
    const { edit, schoolName, titleOfStudy, dateFrom, dateTo } = this.state;
    return (
      <Section headerTitle="Education">
        {edit ? (
          <form onSubmit={this.handleSubmit}>
            <InputField
              onChange={this.handleChange}
              fieldName="schoolName"
              label="School Name"
              placeholder="Westside Technical School"
              value={schoolName}
            />

            <InputField
              onChange={this.handleChange}
              fieldName="titleOfStudy"
              label="Title of Study"
              placeholder="Electrical Engineer"
              value={titleOfStudy}
            />

            <InputField
              onChange={this.handleChange}
              fieldName="dateFrom"
              label="From"
              type="date"
              value={dateFrom}
            />
            <InputField
              onChange={this.handleChange}
              fieldName="dateTo"
              label="To"
              type="date"
              value={dateTo}
            />

            <button type="submit" className="save">
              Save
            </button>
          </form>
        ) : (
          <form onSubmit={this.handleSubmit}>
              <span className="label">School Name: <span className="value">{schoolName}</span></span>
              <span className="label">Title of Study: <span className="value">{titleOfStudy}</span></span>
              <span className="label">From: <span className="value">{dateFrom}</span></span>
              <span className="label">To: <span className="value">{dateTo}</span></span>
            <button type="submit" className="save">
              Edit
            </button>
          </form>
        )}
      </Section>
    );
  }
}

export default Education;
